import React from 'react';
import firebase from 'firebase';
import { Button, Div, List } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

function TestFeed() {
    const [skins, setSkins] = React.useState([])

    React.useEffect(() => {
        const fetchData = async () => {
            const db = firebase.firestore()
            const data = await db.collection("skins").get()
            setSkins(data.docs.map(doc => doc.data()))
        }
        fetchData()
    }, [])

    return (
        <List>
        {skins.map(skin => (
            <Button mode="secondary" key={skin.nickname} key={skin.player_id}>{skin.nickname} {skin.player_id} </Button>
        ))}
</List>
    );
}

export default TestFeed;